import React from 'react';
import './App.css';
import Navigation from './components/Navigation';
import PortfolioSection from './components/PortfolioSection';
import HomeHeroImage from './components/HomeHeroImage';
import HeroImage from './components/HeroImage';
import VistionBackground from './images/vision.png'
import LeadershipBackground from './images/leadership.png'
import StrategyBackground from './images/strategy.png'
import CallOut from './components/CallOut';
import DoubleCallOut from './components/DoubleCallOut';
import Footer from './components/Footer';

class App extends React.Component {

  render() {

    return (
      <div>
      <Navigation/>
          <HomeHeroImage/>
          <PortfolioSection />        
          <HeroImage
            smallCall={'OUR EXECUTIVE'}
            largeCall={'Leadership'}
            linkText={'LEARN ABOUT OUR LEADERSHIP'}
            height={60}
            background={LeadershipBackground}
          />
          <CallOut />
          <HeroImage height={80} background={VistionBackground}/> <HeroImage
          smallCall={'STRAYEGY OVERVIEW'}
          largeCall={'Our Strategy'}
          linkText={'LEARN ABOUT OUR STRATEGY'}
          height={40}
          background={StrategyBackground}
        />
        <DoubleCallOut />
        <Footer />
      </div>
    );
  }
}

export default App;



